<script>
import { actions } from './store';

import Card from 'components/card';
import List from 'components/list';
import Text from 'components/text';
import Message from 'components/message';
import Logout from 'components/logout';
import Dialog from 'components/dialog';

export default {
    components: { Card, List, Text, Message, Logout, Dialog },
    vuex: {
        actions: actions
    },
    // created () {
    //     this.initData();
    // }
}
</script>

<template>
<div id="app">
    <div class="menu">
        <logout></logout>
    </div>
    <div class="sidebar">
        <card></card>
        <list></list>
    </div>
    <div class="main">
        <message></message>
        <text></text>
    </div>
    <dialog></dialog>

    <link href="https://cdn.bootcss.com/emojione/2.2.7/assets/css/emojione.min.css" rel="stylesheet">
    <script src="https://cdn.bootcss.com/emojione/2.2.7/lib/js/emojione.min.js"></script>
</div>
</template>

<style lang="less" scoped>
#app {
    margin: 20px auto;
    width: 800px;
    height: 600px;

    overflow: hidden;
    border-radius: 3px;

    .menu {
        text-align: right;
        margin: 8px auto;
    }
    .sidebar, .main {
        height: 100%;
    }
    .sidebar {
        float: left;
        width: 200px;
        color: #f4f4f4;
        background-color: #2e3238;
    }
    .main {
        position: relative;
        overflow: hidden;
        background-color: #eee;
    }
    .text {
        position: absolute;
        width: 100%;
        bottom: 0;
        left: 0;
    }
    .message {
        height: ~'calc(100% - 160px)';
    }
}
</style>



