<template>
  <div>
    <button class="exporter" @click="exporter">下载聊天记录</button>
    <button class="logout" @click="logout">登出</button>
  </div>
</template>

<script>
import { logout, isAdmin } from '../api'

export default {
  methods: {
    async exporter() {
      if (!await isAdmin()) {
        alert('只有管理员能下载聊天记录')
        return
      }
      var file_path = '/api/history';
      var a = document.createElement('a');
      a.href = file_path;
      a.download = '历史记录.txt';
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
    },
    async logout() {
      await logout()
      location.reload()
    }
  }
}
</script>

<style scoped>
.exporter {
  color: white;
  cursor: pointer;
  width: 20%;
  padding: 10px 0;
  background-color: #0070cd;
  border: 0;
  border-radius: 6px;
}
.logout {
  color: white;
  cursor: pointer;
  width: 20%;
  padding: 10px 0;
  background-color: #0070cd;
  border: 0;
  border-radius: 6px;
}
</style>



