/**
 * Vuex
 * http://vuex.vuejs.org/zh-cn/intro.html
 */
import Vue from 'vue';
import Vuex from 'vuex';
import * as api from './api';

Vue.use(Vuex);

const now = new Date();
const store = new Vuex.Store({
    state: {
        // 当前用户
        user: {
            name: '匿名用户',
            img: 'dist/images/1.jpg',
        },
        // 会话列表
        sessions: [{
            id: '4c4b716b-dbc3-47fe-b750-95dfee776647',
            user: {
                name: 'admin',
                email: 'admin@syx.moe',
                img: 'dist/images/1.jpg',
            },
            messages: [],
            newMsg: false,
        }],
        // 当前选中的会话
        currentSessionId: '4c4b716b-dbc3-47fe-b750-95dfee776647',
        // 过滤出只包含这个key的会话
        filterKey: '',
        // 自己的 UID
        uid: ''
    },
    mutations: {
        INIT_DATA (state, username, sessions) {
            const obj = JSON.parse(JSON.stringify(state.user))
            console.log('username', username);
            obj.name = username
            state.user = obj
            console.log('state.user', state.user);
            state.sessions = sessions
            // let data = localStorage.getItem('vue-chat-session');
            // if (data) {
            //     state.sessions = JSON.parse(data);
            // }
            // DEBUG ONLY //
            // const uid = await api.anonymous()
            // state.uid = uid
            // console.log('DEBUG', state.uid);
            // DEBUG ONLY //
        },
        REFRESH_DATA (state, sessions) {
            if (Object.keys(sessions).length === 0) {
                state.sessions = []
                return;
            }
            let copy = JSON.parse(JSON.stringify(state.sessions));
            const newData = [];
            const toDelete = []
            for (let i = 0; i < sessions.length; i++) {
                const checker = state.sessions.findIndex(item => item.id === sessions[i].id)
                if (checker === -1) {
                    newData.push(sessions[i]);
                }
            }
            for (let i = 0; i < state.sessions.length; i++) {
                const checker = state.sessions.findIndex(item => item.id === sessions[i].id)
                if (checker === -1) {
                    copy[i] = 'del'
                }
            }
            copy = copy.filter(p => p !== 'del');
            state.sessions = copy.concat(newData);
            console.log('merged', state.sessions);
            // state.sessions = sessions
        },
        // 发送消息
        SEND_MESSAGE ({ sessions, currentSessionId }, content) {
            api.sendMessage(currentSessionId, content);
            let session = sessions.find(item => item.id === currentSessionId);
            session.messages.push({
                content: content,
                date: new Date(),
                self: true
            });
        },
        // 接收消息
        PUSH_MESSAGE ({ sessions, /* currentSessionId */ }, uid, content) {
            let session = sessions.find(item => item.id === uid);
            session.messages.push({
                content: content,
                date: new Date(),
                self: false
            });
            session.newMsg = true;
        },
        // 选择会话
        SELECT_SESSION (state, id) {
            state.currentSessionId = id;
            let session = state.sessions.find(item => item.id === id);
            session.newMsg = false;
        },
        // 搜索
        SET_FILTER_KEY (state, value) {
            state.filterKey = value;
        }
    }
});

store.watch(
    (state) => state.sessions,
    (val) => {
        console.log('CHANGE: ', val);
        localStorage.setItem('vue-chat-session', JSON.stringify(val));
    },
    {
        deep: true
    }
);

export default store;
export const actions = {
    initData: async ({ dispatch }) => {
        const username = await api.getUsername();
        console.log('from action', username);
        const users = await api.fetchList();
        const sessions = [];
        users.forEach(p => {
            if (!p.uid || !p.name) {
                return
            }
            sessions.push({
                id: p.uid,
                user: {
                    name: p.name,
                    email: p.email,
                    img: 'dist/images/3.jpg',
                },
                messages: [],
            });
        });
        dispatch('INIT_DATA', username, sessions);
    },
    refreshData: async ({ dispatch }) => {
        const users = await api.fetchList();
        const sessions = [];
        users.forEach(p => {
            if (!p.uid || !p.name) {
                return
            }
            sessions.push({
                id: p.uid,
                user: {
                    name: p.name,
                    email: p.email,
                    img: 'dist/images/3.jpg',
                },
                messages: [],
            });
        });
        dispatch('REFRESH_DATA', sessions);
    },
    sendMessage: ({ dispatch }, content) => dispatch('SEND_MESSAGE', content),
    pushMessage: ({ dispatch }, uid, content) => dispatch('PUSH_MESSAGE', uid, content),
    selectSession: ({ dispatch }, id) => dispatch('SELECT_SESSION', id),
    search: ({ dispatch }, value) => dispatch('SET_FILTER_KEY', value)
};



