'use strict'

import superagent from 'superagent'
import emojione from 'emojione'
// import * as store from '../store'

// REST 部分

let isAnonymous = null
let _isLogged = null
let _isAdmin = null
let username = null
let uid = null

export async function signup(email, username, password) {
  const res = await superagent
    .post('/api/signup')
    .accept('json')
    .type('form')
    .send({
      email,
      username,
      password,
    })
  if (res.body.msg != null) {
    throw new Error(res.body.msg)
  }
  uid = res.body.uid
  return uid
}

export async function login(username, password) {
  const res = await superagent
    .post('/api/login')
    .accept('json')
    .type('form')
    .send({
      username,
      password,
    })
  if (res.body.msg != null) {
    throw new Error(res.body.msg)
  }
  uid = res.body.uid
  _isAdmin = res.body.isAdmin
  return uid
}

export async function anonymous() {
  isAnonymous = true
  const res = await superagent
    .get('/api/anonymous')
    .accept('json')
  uid = res.body.uid
  return uid
}

export async function logout() {
  // if (isAnonymous) {
  //   location.reload()
  //   return
  // }
  if (await isLogged()) {
    return superagent
      .get('/api/logout')
  }
}

export async function fetchList() {
/*
  let cookie = document.cookie
  if (!/auth/i.test(cookie)) {
    cookie = '(anonymous)'
  }
*/
  const res = await superagent
    .get('/api/onlineList')
//    .set('Cookie', cookie)
    .accept('json')
  return res.body.users
}

export function clearAuthCache() {
  // isAnonymous = null // 不能清掉这个
  _isLogged = null
  _isAdmin = null
  username = null
  // uid = null // 不能清掉这个
}

async function checkAuth(force) {
  if (isAnonymous) {
    _isAdmin = false
    _isLogged = true
    username = '匿名用户' + uid.substring(0, 6)
    return
  }
  try {
    const res = await superagent
      .get('/api/auth')
      .accept('json')
    _isAdmin = res.body.isAdmin || false
    _isLogged = res.body.isLogged || false
    uid = res.body.uid || ''
    console.log('from raw rest', res.body);
    username = res.body.username || ('匿名用户' + uid.substring(0, 6))
  } catch (_) {
    _isAdmin = false
    _isLogged = false
    uid = ''
    username = '匿名用户'
  }
}

export async function isAdmin() {
  if (_isAdmin == null) {
    await checkAuth()
  }
  return _isAdmin || false
}

export async function isLogged() {
  if (_isLogged == null) {
    await checkAuth()
  }
  return _isLogged || false
}

export async function getUsername() {
  if (username == null) {
    await checkAuth()
  }
  console.log('from api', username);
  return username || ''
}

export async function getUid() {
  if (uid == null) {
    await checkAuth()
  }
  console.log('from api getuid', uid);
  return uid || ''
}

// WebSocket 部分

const wsUrl = (location.protocol === 'https:' ? 'wss://' : 'ws://') + location.host + '/ws';
let socket;

export async function dial() {
  console.log('尝试连接 ' + wsUrl);
  socket = new WebSocket(wsUrl);
  const uid = await getUid();
  socket.onopen = (ev) => {
    console.log('连接已建立');
    socket.send(uid);
  };
  socket.onmessage = onmessage;
  socket.onclose = () => {
    console.log('连接已中断');
  };
}

let func;

export function registerOnMessage(f) {
  func = f;
}

function onmessage({ data }) {
  const payload = JSON.parse(data);
  func(payload);
}

export async function sendMessage(toUID, content) {
  if (socket == null) {
    throw new Error('WS 连接尚未建立');
  }
  const payload = {
    fromName: await getUid(),
    toName: toUID,
//    time: new Date().toString(),
    text: content,
  };
  console.log('payload', payload);
  socket.send(JSON.stringify(payload));
}

// 杂项

const entityMap = {
  '&': '&amp;',
  '<': '&lt;',
  '>': '&gt;',
  '"': '&quot;',
  "'": '&#39;',
  '/': '&#x2F;',
  '`': '&#x60;',
  '=': '&#x3D;'
};

function escapeHTML(str) {
  return String(str).replace(/[&<>"'`=\/]/g, s => entityMap[s]);
}

export function enforceEmoji(raw) {
  return emojione.toImage(escapeHTML(raw));
}



