<template>
  <div class="modal">
    <div class="modal-content">
      <!-- <span class="close" @click="close">&times;</span> -->
      <div :hidden="!isSignupTab" class="container">
        <input v-model="email" type="text" placeholder="邮箱" required>
      </div>
      <div class="container">
        <input v-model="username" type="text" placeholder="用户名" required>
      </div>
      <div class="container">
        <input v-model="password" type="password" placeholder="密码" required>
      </div>

      <div class="container" style="margin-top:24px">
        <button :hidden="isSignupTab" @click="login">登录</button>
        <button :hidden="!isSignupTab" @click="signup">注册</button>
        <button
          @click="anonymousLogin"
          style="width:40%;background-color:#eab208;color:white"
        >
          以匿名身份登录
        </button>
      </div>

      <div class="container">
        <a
          href="javascript:void(0)"
          :hidden="!isSignupTab"
          @click="isSignupTab = !isSignupTab"
        >
          已有账号？点此登录
        </a>
        <a
          href="javascript:void(0)"
          :hidden="isSignupTab"
          @click="isSignupTab = !isSignupTab"
        >
          没有账号？点此注册
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import { actions } from '../store';
import * as api from '../api'

export default {
  data() {
    return {
      isSignupTab: true,
      email: '',
      username: '',
      password: '',
    }
  },
  vuex: {
      actions,
  },
  methods: {
    async login() {
      try {
        await api.login(this.username, this.password)
        await this.init()
      } catch (err) {
        alert(err.message)
      }
    },
    async signup() {
      try {
        await api.signup(this.email, this.username, this.password)
        await this.init()
      } catch (err) {
        alert(err.message)
      }
    },
    async anonymousLogin() {
      try {
        await api.anonymous()
        await this.init()
      } catch (err) {
        alert(err.message)
      }
    },
    async init() {
      this.$el.style.display = 'none'
      api.clearAuthCache()
      await this.initData()
      setInterval(() => this.refreshData(), 1000)
      await api.dial()
    }
  },
  // Vue2: mounted
  async compiled() {
    if (!await api.isLogged()) {
      this.$el.style.display = 'block'
    } else {
      await this.init()
    }
  }
}
</script>

<style scoped>
.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

/* Modal Content/Box */
.modal-content {
  text-align: center;
  background-color: #fefefe;
  margin: 15% auto; /* 15% from the top and centered */
  padding: 20px;
  border: 1px solid #888;
  width: 40%; /* Could be more or less, depending on screen size */
  border-radius: 40px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
}

.container {
  margin-top: 10px;
}

input {
  padding: 15px 8px;
  height: 48px;
  width: 80%;
}

button {
  color: white;
  cursor: pointer;
  width: 20%;
  padding: 10px 0;
  background-color: #0070cd;
  border: 0;
  border-radius: 6px;
}

/* The Close Button */
/*.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

.close:hover,
.close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}*/
</style>



